.dropdown {
  width: 93px;
  height: 34px;
  color: #fd5103;
  border: 1px solid #fd5103;
  border-radius: 4px;
  position: relative;
  top: 50%;
  left: 40%;
}

.dropdown:hover, .dropdown:focus {
  color: var(--primary-white-color);
  background-color: #fd5103;
}

.dropdown input {
  width: 100%;
  height: 100%;
  cursor: pointer;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #fd5103;
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 6px 10px;
  font-family: Poppins;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.35;
  position: absolute;
  top: 0;
  left: 0;
}

.dropdown input:hover, .dropdown input:focus {
  color: var(--primary-white-color);
  background-color: #fd5103;
}

.dropdown__icon {
  fill: currentColor;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  right: 8px;
  transform: translateY(-50%);
}

::-ms-input-placeholder {
  color: #fd5103;
  font-family: Poppins;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.35;
}

::placeholder {
  color: #fd5103;
  font-family: Poppins;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.35;
}

.dropdown .options {
  width: 100%;
  height: 84px;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  -webkit-backdrop-filter: blur(4.5px);
  backdrop-filter: blur(4.5px);
  -webkit-backdrop-filter: blur(4.5px);
  background: rgba(255, 255, 255, .55);
  border: 1px solid #fd5103;
  border-radius: 4px;
  font-size: 10px;
  line-height: 1.5;
  transition: all .7s;
  position: absolute;
  top: 33px;
  overflow: scroll;
  transform: translateY(-50px);
}

.dropdown .options .option {
  color: #5f6775;
  border-radius: 4px;
  padding: 6.5px 10px;
  transition: all .2s;
}

.dropdown .options .option:not(:last-of-type) {
  margin-bottom: 8px;
}

.dropdown .options .option:hover {
  color: var(--primary-white-color);
  background: #fd5103;
}

.dropdown.opened .options {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.dropdown.opened .dropdown__icon {
  z-index: 1;
  fill: #fd5103;
}

